import React from "react";
import Box from "@material-ui/core/Box";
import { Container, Card, Grid, Button, withStyles, IconButton, FormControl, InputLabel, OutlinedInput, Typography } from "@material-ui/core";
import { ThemeProvider } from "@material-ui/core/styles";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import CircularProgress from "@material-ui/core/CircularProgress";
import { firebaseAuth } from "../utils/firebase";
import firebase from "firebase";

const formInputCommonStyle = {
    "& label.Mui-focused": {
        color: "##1E90FF",
    },
    "& .MuiInput-underline:after": {
        borderBottomColor: "##1E90FF",
    },
    "& .MuiOutlinedInput-root": {
        "& fieldset": {
            borderColor: "#1E90FF",
        },
        "&:hover fieldset": {
            borderColor: "#1E90FF",
        },
        "&.Mui-focused fieldset": {
            borderColor: "##1E90FF",
        },
    },
};

const useStyles = (theme) => ({
    root: {
        "& .MuiTextField-root": {
            margin: theme.spacing(1),
        },
    },
    demoWrapText: {
        backgroundColor: "#1E90FF",
        color: "#ffffff",
        textTransform: "none",
        fontSize: "23px",
        top: "0",
        left: "0",
        width: "100%",
        height: "300px",
        opicity: 1,
        position: "absolute",
    },
    demoWrapImg: {
        width: "100%",
        margin: "280px auto",
        padding: "10px",
        zIndex: 2,
        position: "relative",
        textAlign: "center",
        marginBottom: "0",
        background: "#ffffff",
        borderRadius: "5%",
        maxWidth: "100%"
    },
    commonInputClass: {
        ...formInputCommonStyle,
        width: "100%",
    },
    wrapper: {
        margin: theme.spacing(1),
        position: "relative",
    },
    buttonSuccess: {
        backgroundColor: "green[500]",
        "&:hover": {
            backgroundColor: "green[700]",
        },
    },
    buttonProgress: {
        color: " green[500]",
        position: "absolute",
        top: "70%",
        left: "50%",
        marginTop: -12,
        marginLeft: -12,
    },
    title: {
        "line-height": "55px",
        "-webkit-line-clamp": 4,
        "-webkit-box-orient": "vertical",
        "text-overflow": "ellipsis",
        "overflow": "hidden",
        "display": "-webkit-box",
    }
});

class RecoveryAccountWithPhoneNo extends React.Component {

    constructor(props) {
        super(props);
        this.inputStyles = { marginBottom: "20px" };
        this.state = {
            verificationCode: "",
            phoneNo: "",
            open: false,
            errorMessage: null,
            loading: false,
            memberForRecovery: {}
        };
    }
    handleChangeFormData(e) {
        const inputName = e.target.name;
        this.setState({ [inputName]: e.target.value.trim() });
    }

    handleClose(_event, reason) {
        if (reason === "clickaway") {
            return;
        }
        this.setState({ open: false });
    }

    componentDidMount() {
        const self = this;
        const userName = this.props.match.params.userName;
        const memberUrl = `https://tarabartest.firebaseio.com/Members/${userName}.json`;
        this.setState({ loading: true });

        fetch(memberUrl)
            .then(function (body) {
                return body.text();
            }).then((result) => {
                if (result && result !== "null" && result !== "") {
                    result = JSON.parse(result);
                    if (!result.phoneNo) {
                        return;
                    }
                    self.setState({ phoneNo: result.phoneNo, loading: false, memberForRecovery: result });
                    const applicationVerifier = new firebase.auth.RecaptchaVerifier("recaptcha-container", {
                        size: "invisible",
                        callback: (response) => {
                            console.log("success", response);
                        }
                    });
                    let phNo = "";
                    if (self.state.phoneNo.startsWith("09")) {
                        const ss = self.state.phoneNo.split("09");
                        for (let s = 1; s < ss.length; s++) {
                            phNo += ss[s];
                        }
                    }
                    phNo = `+959${phNo}`;
                    firebaseAuth().signInWithPhoneNumber(phNo, applicationVerifier)
                        .then((confirmResult) => {
                            console.log("success", confirmResult);
                            window.confirmationResult = confirmResult;
                        })
                        .catch((error) => {
                            console.log(error);
                            self.setState({ open: true, errorMessage: "Invalid phone No.", loading: false });
                        });
                } else {
                    self.setState({ open: true, errorMessage: "User does not exist, please register now.", loading: false });
                }
            }, (error) => {
                console.error("performRecoveryAccountWithPhoneNo()", error);
                self.setState({ open: true, errorMessage: "Sorry, server cannot be communicated.", loading: false });
            });
    }

    performRecoveryAccountWithPhoneNo(e) {
        e.preventDefault();
        const self = this;
        window.confirmationResult.confirm(this.state.verificationCode)
            .then(function (result) {
                console.log("Confirmation verification is successfully!");
                window.location.href = window.ref;
            }).catch(function (error) {
                console.log(error);
                self.setState({ open: true, errorMessage: "Wrong Verifiaction Code. Please, try again.", loading: false });
            });
    }

    render() {
        const { classes, theme } = this.props;
        return <ThemeProvider theme={theme}>
            <Box id="recaptcha-container"></Box>
            <Card style={{ background: "white" }}>
                <Box>
                    <Container className={classes.demoWrapImg}>
                        <Grid container>
                            <Grid item xs={1} md={4} lg={4} >
                                <Box style={{ width: "100%" }}></Box>
                            </Grid>
                            <Grid item xs={10} md={4} lg={4} >
                                <form autoComplete="off" onSubmit={(e) => this.performRecoveryAccountWithPhoneNo(e)}>
                                    <p style={{ textAlign: "center", fontSize: "18px", color: "#1E90FF" }}>Verification Code</p>
                                    <p style={{ textAlign: "center", fontSize: "16px", color: "rgb(117 167 216)" }}>Please type the verification code sent to {this.state.phoneNo}</p>
                                    <FormControl
                                        variant="outlined" className={classes.commonInputClass}>
                                        <InputLabel htmlFor="outlined-adornment-verification">Verification Code</InputLabel>
                                        <OutlinedInput
                                            name="verificationCode"
                                            required
                                            id="outlined-adornment-verification"
                                            type="text"
                                            value={this.state.verificationCode}
                                            onChange={(e) => this.handleChangeFormData(e)}
                                            labelWidth={74}
                                        />
                                    </FormControl>
                                    <Button type="submit" variant="text" style={{ backgroundColor: "#1E90FF", color: "#fff", textTransform: "none", width: "100%", fontSize: "23px", margin: "10mm 0 5mm 0", opacity: (this.state.loading ? "0.5" : "1") }}>
                                        Verify
                                    </Button>
                                    {this.state.loading && <CircularProgress size={30} className={classes.buttonProgress} />}
                                </form>
                            </Grid>
                        </Grid>
                    </Container>
                    <Box className={classes.demoWrapText} >
                        <IconButton onClick={() => { this.props.history.goBack(); }}>
                            <ArrowBackIcon style={{ color: "#ffffff" }} />
                        </IconButton>
                        <Box m={2}>
                            <Typography variant="h4" align="center" className={classes.title}>
                                Password Recovery
                            </Typography>
                        </Box>
                    </Box>
                </Box >
                <Snackbar open={this.state.open} autoHideDuration={6000} onClose={() => this.handleClose()}>
                    <MuiAlert elevation={6} variant="filled" onClose={() => this.handleClose()} severity="error">
                        {this.state.errorMessage || "Sorry, something went wrong."}
                    </MuiAlert>
                </Snackbar>

            </Card>
        </ThemeProvider >;
    }
}

export default withStyles(useStyles, { withTheme: true })(RecoveryAccountWithPhoneNo);
